import { Injectable } from '@angular/core';
import { PositionListDataStorage } from './position-list.datastorage';
import { RestfulService } from '@farris/devkit';
import { SysManagerUtil } from '../../../common';
import { PositionListUIState } from '../app/position-list.uistate';

@Injectable()
export class PositionListDataService {
    constructor (
        private rest: RestfulService,
        private dataStorage: PositionListDataStorage,
        private posSession: SysManagerUtil
    ) { }

    /**
     * 加载岗位列表
     */
    loadData (): void {
        const posUrl = this.posSession.positionWebApi;
        const headers = this.posSession.getHeader();
        this.rest.get(posUrl + '/all', null, headers).subscribe(
            (result: any) => {
            this.dataStorage.load(result);
        });
    }

    /**
    * 搜索岗位
    */
    search(begin: string, end: string): void {
    }

    /**
     * 删除
     */
    remove (ids: string[]) {
        const deleteId = ids[0];
        const posUrl = this.posSession.positionWebApi;
        const headers = this.posSession.getHeader();
        this.rest.delete(`${ posUrl }/${ deleteId }`, null, headers).subscribe(
            (result: any) => { 
                this.loadData();
            },
            error => { console.log('删除岗位时出错'); }
        );
        
    }
}



