import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { PositionListRepository } from '../domain/postition-list.repository';
import { GSPPosition } from '../domain/entities/gspposition.entity';


/**
 * 岗位列表BindingData
 */
@Injectable()
@NgBindingData({
    entity: GSPPosition,
    repository: PositionListRepository
})
export class PositionListBindingData extends BindingData {
    constructor(injector: Injector) {
        super(injector);
    }
}



