
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { PositionListDataService } from '../../domain/position-list.dataservice';



@Injectable()
@NgCommandHandler({
  commandName: 'posListFormLoad'
})
export class PosListFormLoadHandler extends CommandHandler {

  constructor(private dataService: PositionListDataService) {
    super();
  }

  schedule() {
    this.addTask('loadData', () => {
      this.dataService.loadData();
    });
  }
}



