import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { PositionListDataStorage } from './position-list.datastorage';
import { GSPPosition } from './entities/gspposition.entity';

/**
 * 岗位列表持久化
 */
@Injectable()
@NgRepository({
  from:  PositionListDataStorage,
  entityType: GSPPosition
})
export class PositionListRepository extends Repository<GSPPosition> {
  constructor(injector: Injector) {
    super(injector);
  }
}



