import { Injectable } from '@angular/core';

@Injectable()
/**二元关系实体增量包 */
export class ResultPackage {

    constructor () {
        this.removingResults = new Array<string>();
        this.addingResults = new Array<string>();
    }
    /**主体内码 */
    ownerId: string;
    /**授权的客体删除列表 */
    removingResults: Array<string>;
    /**授权的客体增加列表 */
    addingResults: Array<string>;
}



