import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { PositionListForm } from '../position-list.form';
import { PositionListDataService } from '../../domain/position-list.dataservice';

@Injectable()
@NgCommandHandler({
    commandName: 'posListSearch'
})
export class PosListSearchHandler extends CommandHandler {

    constructor(
        private dataService: PositionListDataService,
        private form: PositionListForm,
    ) {
        super();
    }

    schedule() {
        this.addTask('loadData', () => {
            let begin = '';
            let end = '';
            this.dataService.search(begin, end);
        });
    }
}



