import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPPosition } from './entities/gspposition.entity';
import { PositionDetailDataStorage } from './position-detail.datastorage';

@Injectable()
@NgRepository({
  from: PositionDetailDataStorage,
  entityType: GSPPosition
})
export class PositionDetailRepository extends Repository<GSPPosition> {
  constructor(injector: Injector) {
      super(injector);
  }
}



