import { Entity, NgField, EntityList, NgList } from '@farris/devkit';
import { GSPRole } from './gsprole.entity';
import { GSPUser } from './gspuser.entity';

/**
 * 岗位实体
 */
export class GSPPosition extends Entity {

  /**
   * 内码
   */
  @NgField(true)
  id: string;

  /**
   * 编号
   */
  @NgField()
  code: string;


  /**
   * 名称
   */
  @NgField()
  name: string;


    /**
   * 描述
   */
  @NgField()
  note: string;


  /**
   * 岗位类型
   */
  @NgField()
  posType: string;

  /**
  * 岗位分组
  */
  @NgField()
  posGroup: string;

  @NgField()
  creatorName: string;
  /**
   * 创建人
   */
  @NgField()
  creator: string;

  /**
   * 创建日期
   */
  @NgField()
  createdTime: string;

  /**
   * 最后修改人
   */
  @NgField()
  lastModifier: string;

  /**
   * 最后修改时间
   */
  @NgField()
  lastModifiedTime: string;

  /**
   * 功能组列表
   */
  @NgList({
    dataField: 'Roles',
    type: GSPRole
  })
  Roles: EntityList<GSPRole>;

  /**
   * 用户列表
   */
  @NgList({
    dataField: 'Users',
    type: GSPUser
  })
  Users: EntityList<GSPUser>;
}



