import {  OnInit, Component, ViewChild } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { PositionDetailViewModel } from './app/position-detail.viewmodel';
import { PosRoleComponent } from './position-role/pos-role.component';

@Component({
  selector: 'app-position-detail',
  templateUrl: './position-detail.component.html'
})
export class PositionDetailComponent implements OnInit {
  // 岗位授权功能组的子组件
  @ViewChild(PosRoleComponent)
  private posRoleChild: PosRoleComponent;
  constructor(
    public viewModel: PositionDetailViewModel,
    private route: ActivatedRoute
  ) {
  }

  ngOnInit() {

    // TODO:
    // 1、路由应该有统一的封装；
    // 2、无法直接给Command传递参数，所以先将dataId写入中间层，再在CommandHandler中获取，过于麻烦；
    // 3、CommandHandler没有返回值，switchMap这种操作符，无法起到应有的作用，所以暂时不用。
    this.route.paramMap.subscribe((params: Params) => {
      this.viewModel.uiState.dataId = params.get('id');
      this.viewModel.uiState.action = params.get('action');
      this.viewModel.formLoad();
    });
  }

  /**
   * 保存岗位和岗位授权的功能组信息
   */
  savePosInfo () {
    // 先还原
    this.viewModel.uiState.needSavePosRole = false;
    if (this.posRoleChild) {
      this.viewModel.uiState.needSavePosRole = true;
    }
    this.viewModel.save();
  }
}



