import { SysManagerUtil } from '../../../../common/util/sys-manager-util';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { Observable } from 'rxjs/Observable';
import { GSPPosRoleUIState } from '../app/gsp-pos-role.uistate';
import { GSPPosRoleRepository } from './gsp-pos-role.repository';
import { ResultPackage } from '../../../../common';



/**功能组岗位关联关系获取功能组数据 */
@Injectable()
export class GSPPosRoleService {

    constructor (
        private rest: RestfulService,
        private posRoleState: GSPPosRoleUIState,
        private sessionUtil: SysManagerUtil,
        private posRoleRep: GSPPosRoleRepository) { }


    /**根据岗位内码获取分配的功能组 */
    getRolesByPosId (posId: string): Observable<any[]> {
        const headers = this.sessionUtil.getHeader();
        const getRoleByUserUrl = this.sessionUtil.positionRoleWebApi + '/positions/' + posId + '/roles';
        return this.rest.get(getRoleByUserUrl, null, headers);
    }

    /**修改功能组和岗位的关联关系 */
    modifyRoleByPosition (): Observable<any> {
        const posId = this.posRoleState.curPositionId;
        const curRoleIdList = this.posRoleRep.getAllEntities().map(item => item.id);
        const roleIdBackUpList = this.posRoleState.roleIdBackUpList;
        // 获取新增和删除的,这个地方需要新增一个可观察者对象
        const addList = curRoleIdList.filter(item => roleIdBackUpList.indexOf(item) < 0);
        const removeList = roleIdBackUpList.filter(item => curRoleIdList.indexOf(item) < 0);
        const posRolePkg = new ResultPackage();
        posRolePkg.ownerId = posId;
        posRolePkg.addingResults = addList;
        posRolePkg.removingResults = removeList;
        // 无需走请求
        if (addList.length === 0 && removeList.length === 0) {
            return new Observable(observer => observer.next());
        }
        console.log('add');
        console.log(addList);
        console.log('remove');
        console.log(removeList);
        const headers = this.sessionUtil.getHeader();
        const posRoleWebUrl = this.sessionUtil.positionRoleWebApi;
        const queryParams = {
            subjectType: 'position',
            objectType: 'role'
        };
        return this.rest.post(posRoleWebUrl, posRolePkg, queryParams, headers)
            .map(item => {
                this.posRoleState.roleIdBackUpList = curRoleIdList;
                return item;
            });
    }

    /**获取所有功能组 */
    getAllRoles (): Observable<any> {
        const headers = this.sessionUtil.getHeader();
        const roleUrl = this.sessionUtil.roleWebApi + '/all';
        return this.rest.get(roleUrl, null, headers);
    }
}



