import { Provider } from '@angular/core';
import { commandHandlers } from './handlers';
import { PositionDetailBindingData } from './position-detail.bindingdata';
import { PositionDetailStateMachine } from './position-detail.statemachine';
import { PositionDetailViewModel } from './position-detail.viewmodel';
import { PositionDetailUIState } from './position-detail.uistate';
import { PositionDetailForm, PositionRoleForm, PositionUserForm } from './position-detail.form';


const POS_DETAIL_APP_PROVIDERS: Provider[] = [
  PositionDetailBindingData,
  PositionRoleForm, PositionDetailForm, PositionUserForm,
  PositionDetailStateMachine,
  PositionDetailViewModel,
  PositionDetailUIState
];

export {
  PositionDetailBindingData,
  PositionRoleForm, PositionDetailForm, PositionUserForm,
  PositionDetailStateMachine,
  PositionDetailViewModel,
  PositionDetailUIState,

  POS_DETAIL_APP_PROVIDERS,
  commandHandlers
};



