import { Component, OnInit } from '@angular/core';
import { PositionListViewModel } from './position-list.index';
import { ActivatedRoute, Params } from '@angular/router';
import { SysManagerUtil } from '../../common';
import { SelectableSettings, SelectableMode } from '@progress/kendo-angular-grid';


@Component({
  selector: 'app-position-list',
  templateUrl: './position-list.component.html'
})

export class PositionListComponent implements OnInit {

  selectableSettings: SelectableSettings;
  selectedMode: SelectableMode = 'single';

  constructor(
    public viewModel: PositionListViewModel,
    private route: ActivatedRoute,
    private currentSession: SysManagerUtil
  ) {
    this.selectableSettings = {
      checkboxOnly: true,
      mode: this.selectedMode
    };
  }

  ngOnInit() {
     // 框架路由进来的时候获取到funcid
     this.route.paramMap.subscribe((params: Params) => {
      if (params.has('funcid')) {
        this.currentSession.funcId = params.get('funcid');
      }
    });
    // 清空选项
    this.viewModel.uiState.selectedIds = [];
    this.viewModel.formLoad();
  }
}



