import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPPosRoleDataStorage } from './gsp-pos-role.datastorage';
import { GSPRoleEntity } from './gsprole.entity';

/**
 * 功能组仓库持久化层
 */
@Injectable()
@NgRepository({
  from: GSPPosRoleDataStorage,
  entityType: GSPRoleEntity
})
export class GSPPosRoleRepository extends Repository<GSPRoleEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}




