
import { PositionListBindingData } from './position-list.bindingdata';
import { PositionListForm } from './position-list.form';
import { PositionListStateMachine } from './position-list.statemachine';
import { ViewModel, NgViewModel, NgCommand } from '@farris/devkit';
import { PositionListUIState } from './position-list.uistate';
import { Injectable, Injector } from '@angular/core';

/**
 * 岗位视图层
 */
@Injectable()
@NgViewModel({
    children: [],
    binding: PositionListBindingData,
    form: PositionListForm,
    stateMachine: PositionListStateMachine,
})
export class PositionListViewModel extends ViewModel {
    /**
     * uiState
     */
    public uiState: PositionListUIState;
    public stateMachine: PositionListStateMachine;

    /**
     * 构造函数
     */
    constructor(injector: Injector) {
        super(injector);
        this.uiState = this.injector.get<PositionListUIState>(PositionListUIState);
    }

    /**
     * 表单加载
     */
    @NgCommand({
        name: 'posListFormLoad'
    })
    public formLoad() { }

    /**
     * 搜索
    */
    @NgCommand({
        name: 'posListSearch'
    })
    public search() { }

    /**
     * 新增
     */
    @NgCommand({
        name: 'posListRoute',
        params: {
            action: 'add'
        }
    })
    public add() { }

    /**
     * 编辑
     */
    @NgCommand({
        name: 'posListRoute',
        params: {
            action: 'edit'
        }
    })
    public edit() { }

    /**
     * 查看
     */
    @NgCommand({
        name: 'posListRoute',
        params: {
            action: 'view'
        }
    })
    public view() { }

    /**
     * 删除
     */
    @NgCommand({
        name: 'posListRemove',
    })
    public remove() { }

      /**
     * 分配数据权限
     */
    @NgCommand({
        name: 'authassign',
        params: {
            action: 'edit'
        }
    })
    public authassign() { }

     /**
     * 查看数据权限
     */
    @NgCommand({
        name: 'authassign',
        params: {
            action: 'view'
        }
    })
    public authview() { }


}



