
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { PositionListBindingData } from '../position-list.bindingdata';
import { SysManagerUtil } from '../../../../common';
import { PositionListUIState } from '../position-list.uistate';

@Injectable()
@NgCommandHandler({
  commandName: 'posListRoute'
})
export class PosListRouteHandler extends CommandHandler {

  constructor (
    private router: Router,
    private bindingData: PositionListBindingData,
    private posUtil: SysManagerUtil,
    private uistate: PositionListUIState
  ) {
    super();
  }

  schedule() {
    this.addTask('route', (lastResult: any, context: TaskExecutionContext) => {
      const command = context.command;
      const action = command.params.action;

      const selectId = this.uistate.selectedIds[0];
      const posId = (action === 'add') ? 'new' : selectId;

      if (action === 'view' || action === 'edit') {
        if (posId == null || posId.length === 0) {
          alert('请选择对应的岗位');
          return;
         }
       }

      const posUrl = this.posUtil.getPositionManagerRouter();
      this.router.navigate([posUrl, posId, { action }]);
    });
  }
}



