import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';
import { PositionDetailBindingData } from './position-detail.bindingdata';
import { PositionDetailForm } from './position-detail.form';
import { PositionDetailStateMachine } from './position-detail.statemachine';
import { PositionDetailUIState } from './position-detail.uistate';


/**
 * 视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: PositionDetailBindingData,
  form: PositionDetailForm,
  stateMachine: PositionDetailStateMachine
})

export class PositionDetailViewModel extends ViewModel {

  public uiState: PositionDetailUIState;
  public stateMachine: PositionDetailStateMachine;

  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(PositionDetailUIState);
  }


  // ----------------------------------------
  // 主表操作
  // ----------------------------------------

  /**
   * 表单加载
   */
  @NgCommand({
    name: 'formLoad'
   })
  public formLoad() {}

  /**
   * 新增
   */
  @NgCommand({
    name: 'add'
   })
  public add() {}

  /**
   * 编辑
   */
  @NgCommand({
    name: 'edit'
   })
  public edit() {}

  /**
   * 保存
   */
  @NgCommand({
    name: 'save'
   })
  public save() {}

  /**
   * 取消
   */
  @NgCommand({
    name: 'cancel'
   })
  public cancel() {}

  /**
   * 关闭
   */
  @NgCommand({
    name: 'close'
   })
  public close() {}


}




