import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { PositionDetailStateMachine } from '../position-detail.statemachine';
import { PositionDetailUIState } from '../position-detail.uistate';


@Injectable()
@NgCommandHandler({
  commandName: 'edit'
})
class EditHandler extends CommandHandler {

  constructor(
    private uiState: PositionDetailUIState,
    private stateMachine: PositionDetailStateMachine
  ) {
    super();
  }

  schedule() {
    this.addTask('transitState', () => {
      this.uiState.action = 'edit'; 
      this.stateMachine['edit']();
    });
  }
}

export { EditHandler };



