import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { GSPPosition } from '../domain';
import { PositionDetailRepository } from '../domain/position-detail.repository';

@Injectable()
@NgBindingData({
  entity:     GSPPosition,
  repository: PositionDetailRepository
})
export class PositionDetailBindingData extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}




