import { SavePosRoleHandler } from './handlers/gsp-pos-role.save.handler';
import { SetPosRoleHandler } from './handlers/gsp-pos-role.set.handler';
import { FormLoadPosRoleHandler } from './handlers/gsp-pos-role.formload.handler';

import { Provider } from '@angular/core';
import { GSPPosRoleUIState } from './gsp-pos-role.uistate';
import { GSPPosRoleBindingData } from './gsp-pos-role.bindingdata';
import { GSPPosRoleViewModel } from './gsp-pos-role.viewmodel';


const posRole_App_Providers: Provider[] = [
    GSPPosRoleUIState,
    GSPPosRoleBindingData,
    GSPPosRoleViewModel
];

// 导出用户和岗位APP域所有成员
export {
    FormLoadPosRoleHandler,
    SetPosRoleHandler,
    SavePosRoleHandler,
    GSPPosRoleUIState,
    GSPPosRoleBindingData,
    GSPPosRoleViewModel,
    posRole_App_Providers
};



