import { Provider } from '@angular/core';
import { PosListFormLoadHandler } from './app/handlers/form-load.handler';
import { PosListSearchHandler } from './app/handlers/search.handler';
import { PosListRouteHandler } from './app/handlers/route.handler';
import { PosListRemoveHandler } from './app/handlers/remove.handler';
import { AuthAssignHandler } from './app/handlers/authassign.handler';
import { PositionListBindingData } from './app/position-list.bindingdata';
import { PositionListForm } from './app/position-list.form';
import { PositionListStateMachine } from './app/position-list.statemachine';
import { PositionListViewModel } from './app/position-list.viewmodel';
import { PositionListUIState } from './app/position-list.uistate';
import { PositionListDataStorage } from './domain/position-list.datastorage';
import { PositionListRepository } from './domain/postition-list.repository';
import { PositionListDataService } from './domain/position-list.dataservice';
import { GSPPosition } from './domain/entities/gspposition.entity';
import { FormLoadHandler } from '../position-detail/app/handlers/form-load.handler';
import { SaveHandler } from '../position-detail/app/handlers/save.handler';
import { AddHandler } from '../position-detail/app/handlers/add.handler';
import { EditHandler } from '../position-detail/app/handlers/edit.handler';
import { CancelHandler } from '../position-detail/app/handlers/cancel.handler';
import { CloseHandler } from '../position-detail/app/handlers/close.handler';
import { FormLoadPosRoleHandler } from '../position-detail/position-role/app/handlers/gsp-pos-role.formload.handler';
import { SavePosRoleHandler } from '../position-detail/position-role/app/handlers/gsp-pos-role.save.handler';
import { SetPosRoleHandler } from '../position-detail/position-role/app/handlers/gsp-pos-role.set.handler';


/**
 * 列表命令+详情命令
 */
const commandHandlers = [
  PosListFormLoadHandler,
  PosListSearchHandler,
  PosListRouteHandler,
  PosListRemoveHandler,
  FormLoadHandler,
  SaveHandler,
  AddHandler,
  EditHandler,
  CancelHandler,
  CloseHandler,
  AuthAssignHandler,
  FormLoadPosRoleHandler,
  SavePosRoleHandler,
  SetPosRoleHandler
];
// Providers
const POSLIST_APP_PROVIDERS: Provider[] = [
  PositionListBindingData,
  PositionListForm,
  PositionListStateMachine,
  PositionListUIState,
  PositionListViewModel
];
// 导出成员
export {
  PositionListBindingData,
  PositionListForm,
  PositionListStateMachine,
  PositionListUIState,
  PositionListViewModel,
  POSLIST_APP_PROVIDERS,
  commandHandlers
};

const POSLIST_DOMAIN_PROVIDERS: Provider[] = [
    PositionListDataStorage,
    PositionListRepository,
    PositionListDataService
];

export {
    GSPPosition,
    PositionListDataStorage,
    PositionListRepository,
    PositionListDataService,
    POSLIST_DOMAIN_PROVIDERS
};



