import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';
import { GSPPosRoleBindingData } from './gsp-pos-role.bindingdata';
import { GSPPosRoleUIState } from './gsp-pos-role.uistate';
import { GSPPosRoleService } from '../domain';

/**
 * 岗位和功能组关系详情视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: GSPPosRoleBindingData,
})
export class GSPPosRoleViewModel extends ViewModel {

  public uiState: GSPPosRoleUIState;
  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(GSPPosRoleUIState);
  }


  /**
   * 岗位和功能组关系加载
   */
  @NgCommand({
    name: 'posRoleFormLoad'
   })
  public formLoad () { }

  /**设置岗位功能组关系 */
  @NgCommand({
    name: 'setPosRole'
   })
  public setPosRole () { }

  /**保存岗位功能组关系 */
  @NgCommand({
    name: 'savePosRole'
   })
  public savePosRole () { }

}



