import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { PositionDetailDataStorage } from './position-detail.datastorage';
import { SysManagerUtil } from '../../../common';
import { PositionDetailUIState } from '../app/position-detail.uistate';
import { PositionDetailStateMachine } from '../app/position-detail.statemachine';
import { PositionDetailRepository } from './position-detail.repository';
import { GSPPosition } from './entities/gspposition.entity';
import { GSPPosRoleService } from '../position-role/domain';




@Injectable()
export class PositionDetailDataService {

  constructor (
    private rest: RestfulService,
    private positionDetailStore: PositionDetailDataStorage,
    private posSession: SysManagerUtil,
    private posUIState: PositionDetailUIState,
    private posStateMachine: PositionDetailStateMachine,
    private posDetailRep: PositionDetailRepository,
    private posRoleService: GSPPosRoleService
  ) {
  }

  /**
   * 加载数据
   * @param dataId
   */
  loadData(dataId: string) {
    const url = this.posSession.positionWebApi;
    const headers = this.posSession.getHeader();
    return this.rest.get(`${url}/${dataId}`, null, headers).subscribe((result: any) => {
      console.log(result);
      // 返回是否是个列表
      this.positionDetailStore.load(result);
    });
  }

  /**
   * 从服务端返回一个guid
   */
  loadNewData() {
    this.positionDetailStore.clear();
    const url = this.posSession.positionWebApi;
    const headers = this.posSession.getHeader();
    this.rest.get(url + '/new', null, headers).subscribe((result: any) => {
      this.posUIState.dataId = result[0]['id'];
      this.positionDetailStore.load(result);
    });
  }

  /**
   * 保存
   */
  save () {
    const newPos = this.posDetailRep.getAllEntities()[0];
    // 分组的默认值先如下处理
    newPos.posGroup = 'admin';
    if (this.posStateMachine.context.state === 'addState') {
      this.saveAddPos(newPos);
    } else {
      this.saveEditPos(newPos);
    }
  }

  saveAddPos(pos: GSPPosition) {
    const posUrl = this.posSession.positionWebApi;
    const headers = this.posSession.getHeader();
    this.rest.post(posUrl, pos.data, null, headers)
      .subscribe(
        (result: any) => {
          // 这个地方必须要重新load一下，否则再去编辑的时候一些字段没有值服务端校验出错
          this.loadData(result);
          // 需要同步岗位和功能组
          if (this.posUIState.needSavePosRole) {
            this.posRoleService.modifyRoleByPosition()
              .subscribe(() => { });
          }
          // 保存后应该走编辑状态
          this.posStateMachine['edit']();
          alert('岗位保存成功');
      },
      (error: HttpErrorResponse) => {
        alert(error.error.Message);
      }
      );
  }
  /**
   * 保存变更集
   */
  saveEditPos(pos: GSPPosition) {
    const url = this.posSession.positionWebApi;
    const headers = this.posSession.getHeader();
    this.rest.put(url, pos.data, null, headers).subscribe(result => {
      if (this.posUIState.needSavePosRole) {
        this.posRoleService.modifyRoleByPosition()
          .subscribe(() => { });
      }
      this.posStateMachine['edit']();
      alert('岗位保存成功');
    },
    (error: HttpErrorResponse) => {
      alert(error.error.Message);
    });
  }
}



