import { GSPPosRoleRepository } from './../../domain/gsp-pos-role.repository';
import { GSPPosRoleUIState } from './../gsp-pos-role.uistate';
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';


/**
 * 功能组岗位详情页面新增
 */
@Injectable()
@NgCommandHandler({
    commandName: 'setPosRole'
})
export class SetPosRoleHandler extends CommandHandler {

    constructor (
        private posRoleUIState: GSPPosRoleUIState,
        private posRoleRep: GSPPosRoleRepository) {
        super();
    }

    // TODO: 需要展开
    schedule () {
        this.addTask('add', () => {
            // 打开帮助
            this.posRoleUIState.posRoleHelpFlag = !this.posRoleUIState.posRoleHelpFlag;
            const curRoleList = this.posRoleRep.getAllEntities();
            console.log(curRoleList);
        });
    }
}



