import { Entity, NgField } from '@farris/devkit';

/**
 * 用户
 */
export class GSPUser extends Entity {

  /**
   * Id
   */
  @NgField(true)
  id: string;

  /**
   * 编号
   */
  @NgField(true)
  Code: string;


  /**
   * 名称
   */
  @NgField()
  Name: string;

}



