import { Injectable } from '@angular/core';
import { EntityList, CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { PositionDetailRepository } from '../../domain';
import { GSPRole } from '../../domain/entities/gsprole.entity';
import { GSPUser } from '../../domain/entities/gspuser.entity';
import { PositionDetailBindingData } from '../position-detail.bindingdata';

@Injectable()
@NgCommandHandler({
  commandName: 'removeItem'
})
class RemoveItemHandler extends CommandHandler {

  constructor(
    private repository: PositionDetailRepository,
    private bindingData: PositionDetailBindingData,
  ) {
    super();
  }

  schedule() {

    this.addTask('removeItem', (lastResult: any, context: TaskExecutionContext) => {

      // 输入处理
      const command = context.command;
      const listPath = command.params.listPath;

      const dataId = this.bindingData['DDNM'];
      const positionEntity = this.repository.getEntityById(dataId);
      if (listPath === 'Role') {
        this.addRole(positionEntity.Roles);
      } else if (listPath === 'User') {
        this.addUser(positionEntity.Users);
      }
    });
  }

  addRole(roleEntityList: EntityList<GSPRole>) {
    const roleId = this.bindingData['Role'].currentId;
    roleEntityList.remove(roleId);
  }

  addUser(jhEntityList: EntityList<GSPUser>) {
    const userId = this.bindingData['User'].currentId;
    jhEntityList.remove(userId);
  }
}

export { RemoveItemHandler };



