import { StateMachine, NgStatemachine, NgState, State, NgRenderState, RenderState } from "@farris/devkit";
import { Injectable } from '@angular/core';



@Injectable()
@NgStatemachine()
export class PositionListStateMachine extends StateMachine {

    /**
     * 初始状态
     */
    @NgState({
        initialState: true
    })
    init: State;

    @NgRenderState({
        render: (context) => context.state === 'init'
    })

    canAdd: RenderState;

    /**
   * 是否允许编辑
   */
    @NgRenderState({
        render: (context) => context.state === 'init'
    })
    canEdit: RenderState;

    /**
     * 是否允许删除
     */
    @NgRenderState({
        render: (context) => context.state === 'init'
    })
    canRemove: RenderState;

    /**
     * 是否允许查看
     */
    @NgRenderState({
        render: (context) => context.state === 'init'
    })
    canView: RenderState;

}



