
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { PositionListDataService } from '../../domain/position-list.dataservice';
import { PositionListUIState } from '../position-list.uistate';



@Injectable()
@NgCommandHandler({
  commandName: 'posListRemove'
})
export class PosListRemoveHandler extends CommandHandler {

  constructor(
    private dataService: PositionListDataService,
    private uiState: PositionListUIState
  ) {
    super();
  }

  schedule() {

    // 删除选中
    this.addTask('remove', () => {
      const dataIds = this.uiState.selectedIds;
      if (dataIds.length === 0) {
        alert('请选择要删除的岗位');
      }
      this.dataService.remove(dataIds);
    });
  }
}


