import { Injectable } from '@angular/core';

/**
 * 岗位功能组组件UI状态
 */
@Injectable()
export class GSPPosRoleUIState {

    curPositionId: string;
    /** 选择的功能组列表，用于在界面上删除时的单行选择*/
    selectRoleList: string[] = [];
    /**第一次功能组列表备份 */
    roleIdBackUpList: string[] = [];
    /**当前界面上功能组内码 */
    curRoleIdList: string[] = [];
    /** 设置岗位功能组帮助的状态值*/
    posRoleHelpFlag = false;
}




