import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, NgFormControl, Form } from '@farris/devkit';
import { PositionListBindingData } from './position-list.bindingdata';

@Injectable()
@NgForm({
    bindingData: PositionListBindingData
})
export class PositionListForm extends Form {
    constructor(injector: Injector) {
        super(injector);
    }

    /**
   * 开始日期
   */
  @NgFormControl({
    name: 'begin',
  })
  begin: FormControl;

  /**
   * 结束日期
   */
  @NgFormControl({
    name: 'end',
  })
  end: FormControl;
}



