import { Injectable, Injector } from '@angular/core';
import { FormControl, NgControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { PositionDetailBindingData } from './position-detail.bindingdata';



@Injectable()
@NgForm({
  bindingData: PositionDetailBindingData
})

export class PositionRoleForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }
  /**
   * 功能组Id
   */
  @NgFormControl({
    name: 'id',
    binding: 'Roles.id'
  })
  id: FormControl;

  /**
   * 功能组编号
   */
  @NgFormControl({
    name: 'Code',
    binding: 'Roles.Code'
  })
  Code: FormControl;

  /**
   * 功能组名称
   */
  @NgFormControl({
    name: 'Name',
    binding: 'Roles.Name'
  })
  Name: FormControl;
}


@Injectable()
@NgForm({
  bindingData: PositionDetailBindingData
})
export class PositionUserForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * 用户ID
   */
  @NgFormControl({
    name: 'id',
    binding: 'Users.id'
  })
  id: FormControl;

  /**
   * 用户编号
   */
  @NgFormControl({
    name: 'Code',
    binding: 'Users.Code'
  })
  Code: FormControl;

  /**
   * 用户名称
   */
  @NgFormControl({
    name: 'Name',
    binding: 'Users.Name'
  })
  Name: FormControl;

}


@Injectable()
@NgForm({
  bindingData: PositionDetailBindingData
})
export class PositionDetailForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  @NgFormControl({
    name: 'id',
    binding: 'id'
  })
  id: string;

  @NgFormControl({
    name: 'Creator',
    binding: 'creator'
  })
  Creator: string;

  /**
   * 岗位编号
   */
  @NgFormControl({
    name: 'Code',
    binding: 'code'
  })
  Code: string;

  /**
   * 岗位名称
   */
  @NgFormControl({
    name: 'Name',
    binding: 'name'
  })
  Name: string;

    /**
   * 描述
   */
  @NgFormControl({
    name: 'Note',
    binding: 'note'
  })
  Note: string;

  /**
   * 岗位分组Id
   */
  @NgFormControl({
    name: 'PosGroup',
    binding: 'posGroup'
  })
  PosGroup: string;

  /**
   * 岗位分组名称
   */
  @NgFormControl({
    name: 'PosGroupName',
    binding: 'PosGroupName'
  })
  PosGroupName: string;

  /**
   * 岗位类型Id
   */
  @NgFormControl({
    name: 'PosType',
    binding: 'posType'
  })
  PosType: string;

  /**
   * 岗位类型名称
   */
  @NgFormControl({
    name: 'PosTypeName',
    binding: 'PosTypeName'
  })
  PosTypeName: string;

  @NgChildForm({
    name: 'PositionRoleForm',
    formType: PositionRoleForm
  })
  PositionRoleForm: Form;

  @NgChildForm({
    name: 'PositionUserForm',
    formType: PositionUserForm
  })
  PositionUserForm: Form;

}




