import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { PositionDetailDataService } from '../../domain';



@Injectable()
@NgCommandHandler({
  commandName: 'save'
})
export class SaveHandler extends CommandHandler {

  constructor(
    private dataService: PositionDetailDataService

  ) {
    super();
  }

  schedule() {

    this.addTask('save', () => {
      this.dataService.save();
    });

  }

}



