import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPPosRoleService } from '../../domain';

/**
 * 岗位功能组详情页面保存
 */
@Injectable()
@NgCommandHandler({
    commandName: 'savePosRole'
})
export class SavePosRoleHandler extends CommandHandler {

    constructor (
        private posRoleService: GSPPosRoleService) {
        super();
    }

    schedule () {
        this.addTask('save', () => {
            this.posRoleService.modifyRoleByPosition().subscribe(
                result => {
                    alert('岗位授权功能组保存成功');
                },
                error => {
                    console.log(error);
                    console.log('岗位授权功能组失败');
                }
            );
        });
    }
}



