import { Entity, NgField } from '@farris/devkit';

/**
 * 功能组关系实体
 */
export class GSPRoleEntity extends Entity {

  /**
   * 功能组内码
   */
  @NgField(true)
  id: string;

  /**
   * 功能组编号
   */
  @NgField()
  code: string;

  /**
   * 功能组名称
   */
  @NgField()
  name: string;

}



