import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { PositionDetailStateMachine } from '../position-detail.statemachine';


@Injectable()
@NgCommandHandler({
  commandName: 'cancel'
})
class CancelHandler extends CommandHandler {

  constructor(
    private stateMachine: PositionDetailStateMachine
  ) {
    super();
  }

  schedule() {
    this.addTask('cancel', () => {
      console.log('取消');
      // 撤销变更操作
    });

    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['view']();
    });
  }
}

export { CancelHandler };



