import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/from';
import { GSPPosRoleService, GSPPosRoleDataStorage } from '../../domain';
import { GSPPosRoleUIState } from '../gsp-pos-role.uistate';

/**
 * 岗位功能组详情页面加载
 */
@Injectable()
@NgCommandHandler({
    commandName: 'posRoleFormLoad'
})
export class FormLoadPosRoleHandler extends CommandHandler {

    constructor (
        private posRoleService: GSPPosRoleService,
        private posRoleStore: GSPPosRoleDataStorage,
        private posRoleState: GSPPosRoleUIState) {
        super();
    }

    schedule () {
        this.addTask('loadData', () => {
            // 根据用户内码获取分配的岗位
            const dataId = this.posRoleState.curPositionId;
            this.posRoleService.getRolesByPosId(dataId).subscribe((result: any) => {
                // 加载数据
                this.posRoleStore.load(result);
                // 清空数据，然后备份,用于最后保存时的对比比较
                this.posRoleState.roleIdBackUpList = [];
                // 子组件输入属性绑定值，连动当前界面和功能组帮助选择数据
                this.posRoleState.curRoleIdList = [];
                Observable.from(result)
                    .map(item => item['id'])
                    .subscribe(item => {
                        this.posRoleState.roleIdBackUpList.push(item);
                        this.posRoleState.curRoleIdList.push(item);
                    });
            });
        });
    }
}



