import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { GSPPosRoleViewModel, GSPPosRoleUIState } from './app';
import { GSPPosRoleRepository } from './domain';
import { GSPRoleEntity } from './domain/gsprole.entity';

/**
 * 功能组岗位关系列表
 */
@Component({
  selector: 'app-pos-role',
  templateUrl: './pos-role.component.html',
  styleUrls: ['./pos-role.component.css'],
})
export class PosRoleComponent implements OnInit, OnChanges {

  /** 当前岗位内码*/
  @Input()
  curPosId: string;
  selectableSettings: SelectableSettings;
  constructor (
    public posRoleVm: GSPPosRoleViewModel,
    public posRoleState: GSPPosRoleUIState,
    private posRoleRep: GSPPosRoleRepository) {
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'single'
    };
  }
  ngOnInit () {
  }

  ngOnChanges (changes: SimpleChanges): void {
        // 加载数据
    this.posRoleVm.uiState.selectRoleList = [];
    // 一定要给复制
    this.posRoleState.curPositionId = this.curPosId;
    // TODO: 这个地方目前只有一个问题，输入属性触发变化，但是用不上
        this.posRoleVm.formLoad();
   }

  /** 授权功能组的时候回调函数*/
  afterRoleSelected (args: any[]) {
    // 更新值
    const newRoleEntityList = [];
    this.posRoleVm.uiState.curRoleIdList = [];
    Observable.from(args).subscribe(
      item => {
        newRoleEntityList.push(new GSPRoleEntity(item));
        this.posRoleVm.uiState.curRoleIdList.push(item['id']);
      }
    );
    this.posRoleRep.clear();
    this.posRoleRep.addEntities(newRoleEntityList);

  }

}



