
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { PositionDetailUIState } from '../position-detail.uistate';
import { PositionDetailStateMachine } from '../position-detail.statemachine';
import { PositionDetailDataService } from '../../domain/position-detail.dataservice';


@Injectable()
@NgCommandHandler({
  commandName: 'formLoad'
})
class FormLoadHandler extends CommandHandler {

  constructor(
    private dataService: PositionDetailDataService,
    private uiState: PositionDetailUIState,
    private stateMachine: PositionDetailStateMachine
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('loadData', () => {
      const dataId = this.uiState.dataId;
      if (dataId === 'new') {
        this.dataService.loadNewData();
      } else {
        this.dataService.loadData(dataId);
      }
    });

    // 状态迁移
    this.addTask('transitState', () => {
      const action = this.uiState.action;
      this.stateMachine[action]();
    });
  }
}

export { FormLoadHandler };



