import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { GSPPosRoleService } from '../../position-role/domain';


@Component({
  selector: 'app-list-help',
  templateUrl: './list-help.component.html',
  styleUrls: ['./list-help.component.css']
})
export class ListHelpComponent implements OnInit, OnChanges {

  /**帮助标题 */
  @Input()
  title = '帮助';
  /**帮助打开状态 */
  @Input()
  isShow: boolean;
  /**选中列表 */
  @Input()
  initSelectedKeys: string[];

  /**输出事件 */
  @Output()
  selectedAfterOk: EventEmitter<any> = new EventEmitter<any>();

  /**当前界面选中的内码 */
  curSelectedKeys: string[];
  selectableSettings: SelectableSettings;
  data: Object[];

  constructor (private posRoleService: GSPPosRoleService) {
    // 多选模式
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'multiple',
      enabled: true
    };
  }

  ngOnInit () {
  }

  /**输入属性变化后 */
  ngOnChanges (changes: SimpleChanges): void {
    // 只有显示属性变化后才能起作用
    if (changes['isShow']) {
      // 第一次加载页面的时候,会有第一次变化
      if (changes.isShow.firstChange) {
        // 首次进来不取数、不显示
        this.isShow = false;
        return;
      }
      // 按钮触发进来
      this.isShow = true;
      // 服务端获取数据
      this.posRoleService.getAllRoles()
        .subscribe(result => {
          this.data = result;
          this.curSelectedKeys = [];
          if (this.initSelectedKeys) {
            this.initSelectedKeys.map(item => this.curSelectedKeys.push(item));
          }
        }, error => {
          console.log('岗位授权功能组时获取功能组列表失败');
          console.log(error);
        });
    }
  }
  /**帮助确定按钮 */
  btnOk () {
    this.closeDialog();
    // 根据选中行获取实体数据
    const selectRoleEntity = [];
    Observable.from(this.curSelectedKeys)
      .map(item => this.data.find(pos => pos['id'] === item))
      .subscribe(item => selectRoleEntity.push(item));
    // 发射当前选中数据
    this.selectedAfterOk.next(selectRoleEntity);

  }
  showDialog () {
    this.isShow = true;
  }
  closeDialog () {
    this.isShow = false;
  }
}



