import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { SysManagerUtil } from '../../../../common';

@Injectable()
@NgCommandHandler({
  commandName: 'close'
})
class CloseHandler extends CommandHandler {

  constructor(
    private router: Router,
    private posUtil: SysManagerUtil
  ) {
    super();
  }

  schedule() {
    this.addTask('close', () => {
      const posUrl = this.posUtil.getPositionManagerRouter();
      this.router.navigate([posUrl]);
    });
  }
}

export { CloseHandler };



