import { Entity, NgField } from '@farris/devkit';

/**
 * 功能组
 */
export class GSPRole extends Entity {

  @NgField(true)
  id: string;

  /**
   * 编号
   */
  @NgField()
  Code: string;

  /**
   * 名称
   */
  @NgField()
  Name: string;

}



