import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, EntityFactory } from '@farris/devkit';
import { PositionDetailStateMachine } from '../position-detail.statemachine';
import { PositionDetailUIState } from '../position-detail.uistate';
import { PositionDetailBindingData } from '../position-detail.bindingdata';
import { PositionDetailRepository } from '../../domain/position-detail.repository';
import { GSPPosition } from '../../domain/entities/gspposition.entity';

@Injectable()
@NgCommandHandler({
  commandName: 'add'
})
class AddHandler extends CommandHandler {

  constructor(
    private router: Router,
    private stateMachine: PositionDetailStateMachine,
    private uiState: PositionDetailUIState,
    private repository: PositionDetailRepository,
    private bindingData: PositionDetailBindingData
  ) {
    super();
  }

  schedule() {
    this.addTask('add', () => {
      this.uiState.action = 'add';
      //this.router.navigate(['positions', { action: 'add' }]);
      let item = this.bindingData.list.currentItem;
      const newEntity = EntityFactory<GSPPosition>(GSPPosition, item);
      this.repository.addEntity(newEntity);
    });

    this.addTask('transitState', () => {
      this.stateMachine['add']();
    });
  }
}

export { AddHandler };


