import { Entity, NgField } from '@farris/devkit';

/**
 * 岗位实体
 */

export class GSPPosition extends Entity {

    /**
     * 岗位ID
     */
    @NgField(true)
    id: string;

    /**
     * 岗位编号
     */
    @NgField()
    code: string;

    /**
     * 岗位名称
     */
    @NgField()
    name: string;

    /**
     * 岗位类型
     */
    @NgField()
    posType: string;

    /**
     * 岗位分组
     */
    @NgField()
    posGroupName: string;

    /**
     * 创建人
     */
    @NgField()
    creator: string;
    /**
     * 创建人名称
     */
    @NgField()
    creatorName: string;

    /**
     * 创建日期
     */
    @NgField()
    createdTime: string;
    /**
     * 描述
     */
    @NgField()
    note: string;
}



